import React from 'react';
import './App.css';
import {GetTokenRequest,AdministrationServiceClient} from './proto/gen/rocsys/os/local/v2alpha/local_grpc_web_pb.js'


const showToken = async() => {
    const EnvoyURL = "http://localhost:8090";
    var request = new GetTokenRequest();
    const client = new AdministrationServiceClient(EnvoyURL, null, null);
    const response = await client.listNamespaces(request);
    console.log(response);
    return response;
}
const onClickShow = async () => {
    const response = await showToken();
    document.getElementById("response").innerText = JSON.stringify(response.toObject());
}

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <h1>Hello World</h1>
      </header>
      Press Button
      <button onClick={onClickShow}>Show Token</button>
      <div id="response"></div>

    </div>
  );
}



export default App;
